﻿requirejs.config({
    paths: {
        'text': '../lib/require/text',
        'durandal': '../lib/durandal/js',
        'plugins': '../lib/durandal/js/plugins',
        'knockout': '../lib/knockout/knockout-2.3.0',
        'bootstrap': '../lib/bootstrap/js/bootstrap',
        'jquery': '../lib/jquery/jquery-1.9.1',
        'mockjax': '../lib/mockjax/mockjax',
        'dataContext': '/Volumes/DATA/Dropbox/Webservers/bionic/SPA/emailclient/app/data/dataContext',
        'dataService': '/Volumes/DATA/Dropbox/Webservers/bionic/SPA/emailclient/app/data/dataService',
        'dataModel': '/Volumes/DATA/Dropbox/Webservers/bionic/SPA/emailclient/app/data/dataModel',
        'fixtures': '/Volumes/DATA/Dropbox/Webservers/bionic/SPA/emailclient/app/data/fixtures',
        'app_notification': '/Volumes/DATA/Dropbox/Webservers/bionic/SPA/emailclient/app/helpers/notification',
    },
    shim: {
        'bootstrap': {
            deps: ['jquery'],
            exports: 'jQuery'
        }
    },
    urlArgs: 'v=' + Math.random()
});

define('main',['durandal/system', 'durandal/app', 'durandal/viewLocator'], function (system, app, viewLocator) {
    //>>excludeStart("build", true);
    system.debug(true);
    //>>excludeEnd("build");

    app.title = 'Durandal Starter Kit';

    app.configurePlugins({
        router: true,
        widget: true
    });

    app.start().then(function () {

        //Replace 'viewmodels' in the moduleId with 'views' to locate the view.
        //Look for partial views in a 'views' folder in the root.
        viewLocator.useConvention();

        //Show the app by setting the root view model for our application with a transition.
        app.setRoot('shell/index');
    });
});
