define('fixtures',['durandal/system'], function (system) {
    var email = {
        account_id: 'email@domain.com',
        date: "14 Apr 01:19:37",
        from: "user@domain.com",
        isRead: true,
        isFlagged: false,
        subject:"Email subject - very important",
        body: "Hi, username, \nThis is a test message. \nThis is another line of the message. \nAnd another line. \n\nBye!",
        message_id:system.guid()



    };


    function getEmails(){
        var emails=[];

        var email_count=(Math.random()*100).toFixed(0);

        for (var i = 0; i < email_count; i++) {
            var key=(Math.random()*100).toFixed(0);
            var email_copy=JSON.parse(JSON.stringify(email));//copy object by value
            email_copy.isRead=Math.random() >0.5 ? true : false;
            email_copy.date=email.date +key;
            email_copy.from=email.from +key;
            email_copy.subject=email.subject+key;
            email_copy.body=key+email.body;
            email_copy.message_id=system.guid();
            emails.push(email_copy);
        }

        return emails;
    }
    return {
        getEmails: getEmails
    };
});
